import React from 'react'
import arrow from '../../assets/icon_arrow01.svg'
import './styles.scss'

const Arrow = ({ sorted }) => {
    const cls = sorted === 'descending' ? 'tharrow desc' : 'tharrow asc'
    return <img className={cls} src={arrow} alt={'arrow'} />
}

const Th = ({ children, sorted }) => {
    console.log({sorted})
    return (
        <>
        <span className={'th'}>{children}</span>
        {sorted ? <Arrow sorted={sorted} /> : ""}
        </>
    )
}

export default Th