import React, { useState, useEffect, useContext } from 'react'
import _ from 'lodash'

import tableData from '../../data'
import From from '../From'
import To from '../To'
import Results from '../Results'
import RelativeDate from '../RelativeDate'
import Clip from '../Clip'
import Count from '../Count'
import Subject from '../Subject'
import Logo from '../Logo'
import Th from '../Th'

import mailicon from '../../assets/icon_mail_sp.svg'
import arrowicon from '../../assets/icon_arrow02.svg'

import './styles.scss'

import { DatesContext } from '../../contexts/datesContext'
import { WindowsContext } from '../../contexts/windowsContext'

const defaultTableState = {
  column: null,
  data: {},
  direction: null,
}

const Table = () => {
  console.log('Table')
  const { dates } = useContext(DatesContext)
  const { windows, setWindows } = useContext(WindowsContext)
  const [tableState, setTableState] = useState(defaultTableState)
  const { column, data, direction } = tableState

  // sorting ascending or descending when clicking on the table header cell
  const handleSort = (clickedColumn) => () => {

    if (column !== clickedColumn) {
      setTableState({
        column: clickedColumn,
        data: _.sortBy(data, [clickedColumn]),
        direction: 'ascending',
      })

      return
    }

    setTableState({
      column: clickedColumn,
      data: data.reverse(),
      direction: direction === 'ascending' ? 'descending' : 'ascending',
    })
  }

  // handle click on table row
  const handleTableRowClick = (e) => {
    const uuid = e.currentTarget.dataset.uuid
    const window = { maximized: true, uuid } // window object has maximized to open the email window already maximized

    if (windows.find(w => w.uuid === uuid)) {
      return
    }
    const updated = [...windows, window]
    setWindows(updated)
  }

  // filters data for the table every time the dates change
  useEffect(() => {
    const { startDate, endDate } = dates[0]
    const filtered = tableData.filter(obj => {
      const date = new Date(obj.date)
      return date > startDate && date < endDate
    })
    setTableState({ ...tableState, data: filtered })
  }, [dates])


  return (
    <>
      <Results count={data.length}></Results>
      <div className={"table"}>
        {data.length === 0 ? <Logo /> : <table>
          <thead >
            <tr>
              <td
                onClick={handleSort('from')}
                >
                <Th sorted={column === 'from' ? direction : null} >
                  From
                </Th>
              </td>
              <td
                onClick={handleSort('to')}
              >
                <Th sorted={column === 'to' ? direction : null} >
                To
                </Th>
            </td>
              <td
                id={"count"}
                sorted={column === 'count' ? direction : null}
                onClick={handleSort('count')}
              >
              </td>
              <td
                onClick={handleSort('subject')}
              >
                <Th sorted={column === 'subject' ? direction : null} >
                Subject
                </Th>
            </td>
              <td
                id={"attach"}
                sorted={column === 'attachment' ? direction : null}
                onClick={handleSort('attachment')}
              >
              </td>
              <td
                onClick={handleSort('date')}
              >
                <Th sorted={column === 'date' ? direction : null} >
                Date
                </Th>
            </td>
            </tr>
          </thead>
          <tbody>
            {_.map(data, ({ _id, uuid, from, to, count, subject, attachment, date }) => {
              return (<tr key={_id} data-uuid={uuid} onClick={handleTableRowClick}>
                <td className={"mailicon"}><img src={mailicon} alt={"icon"}></img></td>
                <td className={"arrowicon"}><img src={arrowicon} alt={"icon"}></img></td>
                <td className={"from"}><From email={from} /></td>
                <td className={"to"}><To emails={to} />{count ? ', ...' : ""}</td>
                <td className={"count"}><Count count={count} /></td>
                <td className={"subject"}><Subject subject={subject} /></td>
                <td className={"clip"}><Clip clip={attachment} /></td>
                <td className={"date"}><RelativeDate date={date} /></td>
              </tr>)
            }
            )}
          </tbody>
        </table>}
      </div>
    </>
  )
}

export default Table
