import React, { useState, useContext } from 'react'
import ReactDOM from 'react-dom'
import Window from '../Window'
import './styles.scss'
import { WindowsContext } from '../../contexts/windowsContext';

const windowsEl = document.getElementById('windows');

const Windows = React.memo(() => {
    console.log('Windows')
    const { windows } = useContext(WindowsContext)

    const W = () => (
        <>
            {windows.reverse().map(window => { // reverse is so that last clicked window to show on top of the other windows
                const { uuid, maximized } = window;
                return <Window key={uuid} uuid={uuid} maximized={maximized} />
            })}
        </>
    )

    return ReactDOM.createPortal(
        <W />,
        windowsEl
    )

})

export default Windows