import React from 'react'

const RelativeDate = ({ date }) => {
    const locale = navigator.language
    const rtl = new Intl.RelativeTimeFormat(locale, { numeric: "auto" })

    const now = new Date()
    const d = new Date(date)
    let displaydate = ""

    // time difference expressed in different measures
    const mins = Math.round((d - now) / (1000 * 60)) // difference in minutes
    const hours = Math.round(mins / 60) // difference in hours
    const days = Math.round(hours / 24) // difference in days

    if (days < -28) {
        displaydate = d.toLocaleDateString(locale)
    } else if (days <= -1) {
        displaydate = rtl.format(days, 'days')
    } else if (hours <= -1) {
        displaydate = rtl.format(hours, 'hours')
    } else {
        displaydate = rtl.format(mins, 'minutes')
    }

    return (<>{displaydate}</>)
}

export default RelativeDate