import React, { useState, useEffect } from 'react'
import CalendarIcon from '../../assets/icon_calender.svg'
import SearchIcon from '../../assets/icon_search.svg'
import "./styles.scss"

const defaultRange = "2019/01/01 - 2020/02/02"

const DateInput = ({ dates, onClick }) => {
    const [range, setRange] = useState(defaultRange)
    useEffect(() => {
        const locale = navigator.language
        const { startDate, endDate } = dates[0]
        const startDateString = startDate.toLocaleDateString(locale)
        const endDateString = endDate.toLocaleDateString(locale)
        setRange(`${startDateString} - ${endDateString}`)
    }, [dates])

    return (
        <div className={'dateinput'} onClick={onClick} >
            <img className={'dateinput__icon--calendar'} src={CalendarIcon} alt="Calendar" />
            <input readOnly className={'dateinput__input'} value={range} type="text" ></input>
            <img className={'dateinput__icon--search'} src={SearchIcon} alt="Calendar" />
        </div>
    )
}

export default DateInput