import React, { createContext, useState, useMemo } from 'react'

const defaultWindows = []

export const WindowsContext = createContext()

export const WindowsContextProvider = ({ children }) => {
    const [windows, setWindows] = useState(defaultWindows)
    const value = useMemo(() => ({windows, setWindows}), [windows, setWindows])
    return (<WindowsContext.Provider value={value}>{children}</WindowsContext.Provider>)
}