import React from 'react'
import ClipIcon from '../../assets/icon_clip.svg';

const Clip = ({ clip }) => {
    const clipstyle = {
        height: ".8rem",
        width: ".8rem"
    }

    return (
        <div>
            {clip ? <img src={ClipIcon} style={clipstyle} alt="Clip" /> : ""}
        </div>
    )
}

export default Clip