import React, { useState, useEffect, useContext } from 'react';
import { DateRange } from 'react-date-range';
import 'react-date-range/dist/styles.css';
import 'react-date-range/dist/theme/default.css';
import DateInput from '../DateInput'
import './styles.scss'
import { DatesContext} from '../../contexts/datesContext'

const DatePicker = () => {
  console.log('DatePicker')
  const {dates, setDates} = useContext(DatesContext)
  const [showDateRange, setShowDateRange] = useState(false)

  // handle click on the date range input in order to toggle data range picker
  const handleDataInputClick = () => {
    setShowDateRange(!showDateRange)
  }

  // handle click outside daterange picker in order to close it
  const handleClickOutside = (e) => {
    if (!e.target.closest(".daterange") && showDateRange === true) {
      setShowDateRange(false)
    }
    return
  }

  useEffect(() => {
    document.addEventListener('click', handleClickOutside)
    return () => {
      document.removeEventListener("click", handleClickOutside)
    }
  })

  return (
    <div
      className="date"
      style={{ zIndex: 10001 }}
    >
      <DateInput dates={dates} onClick={handleDataInputClick} />
      <DateRange
        id={"daterange"}
        className={showDateRange ? "daterange show" : "daterange"}
        editableDateInputs={true}
        onChange={item => { setDates([item.selection]) }}
        moveRangeOnFirstSelection={false}
        ranges={dates}
        rangeColors={["#dedede"]}
      />
    </div>
  )
}

export default DatePicker