import React, { createContext, useState, useMemo } from 'react'

const defaultDates = [
    {
        startDate: new Date(),
        endDate: new Date(),
        key: 'selection'
    }
]

export const DatesContext = createContext(defaultDates)

export const DatesContextProvider = ({ children }) => {
    const [dates, setDates] = useState(defaultDates);
    const value = useMemo(() => ({dates, setDates}), [dates, setDates])
    return (<DatesContext.Provider value={value}>{children}</DatesContext.Provider>)
}