import React from 'react'
import gh from '../../assets/gh.svg'

const Copy = () => {

    return (
        <span className={'copy'}>&copy; 2020,
            <a href={"https://github.com/axmz/"} target={"_blank"}>axmz
            <img src={gh}></img>
            </a>
        </span>
    )
}

export default Copy